import React from 'react';

import {
    Datagrid,
    List,
    Show,
    Create,
    Edit,
    Filter,
    DisabledInput,
    SimpleShowLayout,
    SimpleForm,
    TextField,
    TextInput,
    ShowButton,
    EditButton,
    DeleteButton,
} from "react-admin";

const ClientFilter = (props) => (
    <Filter {...props}>
        <TextInput label="Search" source="lname" alwaysOn />
    </Filter>
);

export const ClientShow = (props) => (
    <Show {...props}>
        <SimpleShowLayout>
            <TextField source="fname" label="Όνομα" />
            <TextField source="lname" label="Επώνυμο" />
            <TextField source="email" label="email" />
            <TextField source="mobile" label="Τηλέφωνο" />
            <TextField source="debt" label="Οφειλή" />
        </SimpleShowLayout>
    </Show>
);

export const ClientList = props => {
    console.log('ClientList');
    console.log(props);

    return (

        <List {...props} title="Πελάτες" filters={<ClientFilter />}>
            <Datagrid>

                <TextField source="fname" label="Όνομα" />
                <TextField source="lname" label="Επώνυμο" />
                <TextField source="email" label="email" />
                <TextField source="mobile" label="Τηλέφωνο" />
                <TextField source="debt" label="Οφειλή" />
                <EditButton />
                <ShowButton label="" />
                <DeleteButton label="" redirect={false} />
            </Datagrid>
        </List>);
}

export const ClientEdit = props => (
    <Edit {...props}>
        <SimpleForm>
            <DisabledInput source="id" />
            <TextInput source="fname" label="Όνομα" />
            <TextInput source="lname" label="Επώνυμο" />
            <TextInput source="email" label="email" />
            <TextInput source="mobile" label="Τηλέφωνο" />
            <TextInput source="debt" label="Οφειλή" />
        </SimpleForm>
    </Edit>
);

export const ClientCreate = props => (
    <Create {...props}>
        <SimpleForm>

            <TextInput source="fname" label="Όνομα" />
            <TextInput source="lname" label="Επώνυμο" />
            <TextInput source="email" label="email" />
            <TextInput source="mobile" label="Τηλέφωνο" />
            <TextInput source="debt" label="Οφειλή" />
        </SimpleForm>
    </Create>
);