import React from 'react';
import {
    List,
    Edit,
    Create,
    Datagrid,
    TextField,
    EditButton,
    SimpleForm,
    TextInput,
    ImageInput,
    ImageField
} from 'react-admin';

export const UserList = props => (
    <List title="Χρήστες" {...props}>
        <Datagrid>

            <TextField source="name" label="Όνομα" />
            <TextField source="username" />
            <TextField source="email" />
            <EditButton />
        </Datagrid>
    </List>
);

export const UserShow = props => (
    <Create {...props}>
        <SimpleForm>
            <TextField source="id" />
            <TextField source="name" label="Όνομα" />
            <TextField source="username" />
            <TextField source="email" />

        </SimpleForm>
    </Create>
);

export const UserCreate = props => (
    <Create {...props}>
        <SimpleForm>
            <TextInput source="id" />
            <TextInput source="name" />
            <TextInput source="username" />
            <TextInput source="email" />
            <ImageInput source="image" label="Related Image" accept="image/*" multiple>
                <ImageField source="src" title="title" />
            </ImageInput>
        </SimpleForm>
    </Create>
);

export const UserEdit = props => (
    <Edit {...props}>
        <SimpleForm>
            <TextInput source="id" />
            <TextInput source="name" />
            <TextInput source="username" />
            <TextInput source="email" />
            <ImageInput source="image" label="Related Image" accept="image/*" multiple>
                <ImageField source="src" title="title" />
            </ImageInput>
        </SimpleForm>
    </Edit>
);