import React from 'react';
import {
    List,
    Edit,
    Show,
    Create,
    Datagrid,
    ReferenceField,
    DateField,
    EditButton,
    ShowButton,
    DisabledInput,
    ReferenceInput,
    SimpleForm,
    TextInput,
    DateTimeInput,
    SimpleShowLayout,
    FunctionField,
    AutocompleteInput,
    NumberInput
} from 'react-admin';


export const SubscriptionList = props => (
    <List {...props} title="Συνδρομές">
        <Datagrid>
            <ReferenceField source="client" reference="Clients" label="Πελάτης">
                <FunctionField render={client => `${client.lname} ${client.fname}`} />
            </ReferenceField>

            <ReferenceField source="plan" reference="Plans" label="Πακέτο">
                <FunctionField render={plan => {

                    let name = `${plan.name}  ${plan.price}€`;
                    if (plan.months === 1)
                        name = name + ' / μήνα';
                    else
                        name = name + ` / ${plan.months} μήνες`;

                    return name;
                }
                } />
            </ReferenceField>


            <DateField source="from" label="Από" />
            <DateField source="το" label="Έως" />

            <FunctionField render={Subscription => `${Subscription.cost}€`} label="Κόστος" />

            <EditButton />
            <ShowButton />
        </Datagrid>
    </List >
);

export const SubscriptionShow = (props) => (
    <Show {...props}>
        <SimpleShowLayout>
            <ReferenceField source="client" reference="Clients" label="Πελάτης">
                <FunctionField render={client => `${client.lname} ${client.fname}`} />
            </ReferenceField>

            <ReferenceField source="plan" reference="Plans" label="Πακέτο">
                <FunctionField render={plan => {

                    let name = `${plan.name}  ${plan.price}€`;
                    if (plan.months === 1)
                        name = name + ' / μήνα';
                    else
                        name = name + ` / ${plan.months} μήνες`;

                    return name;
                }
                } />
            </ReferenceField>

            <DateField source="from" label="Από" />
            <DateField source="το" label="Έως" />

            <FunctionField render={Subscription => `${Subscription.cost}€`} label="Κόστος" />

        </SimpleShowLayout>
    </Show>
);


export const SubscriptionEdit = props => (
    <Edit {...props}>
        <SimpleForm>
            <ReferenceField source="client" reference="Clients" label="Πελάτης">
                <FunctionField render={client => `${client.lname} ${client.fname}`} />
            </ReferenceField>

            <NumberInput source="amount" label="Ποσό" />
            <DateTimeInput source="date" label="Ημερομηνία" />
        </SimpleForm>
    </Edit>
);

export const SubscriptionCreate = props => (
    <Create {...props}>
        <SimpleForm>
            <DisabledInput source="id" />
            <ReferenceInput
                label="Πελάτης"
                source="client"
                reference="Clients"

            >
                <AutocompleteInput optionText="fname" />
            </ReferenceInput>

            <ReferenceInput
                label="Πακέτο"
                source="plan"
                reference="Plans"

            >
                <AutocompleteInput optionText="name" />
            </ReferenceInput>

            <TextInput source="amount" label="Ποσό" />
            <DateTimeInput source="date" label="Ημερομηνία" />
        </SimpleForm>
    </Create>
);