import React from 'react';

import Typography from '@material-ui/core/Typography';


import Paper from '@material-ui/core/Paper';
import PropTypes from 'prop-types';

import { withStyles } from '@material-ui/core/styles';

import Grid from '@material-ui/core/Grid';

import CardContent from '@material-ui/core/CardContent';
import { Title, Query, Loading } from 'react-admin';

const styles = theme => ({
    root: {
        flexGrow: 1,
        maxWidth: 600,
    },
    paper: {
        padding: theme.spacing.unit * 2,
        maxWidth: 600,
        textAlign: 'left',
        color: theme.palette.text.secondary,
    },
});

function CenteredGrid(props) {
    const { classes } = props;

    const payload = {
        pagination: { page: 1, perPage: 100 },
        sort: { field: 'lname', order: 'ASC' },

    };

    const UserList = () => (
        <Query type="GET_LIST" resource="Clients" payload={payload}>
            {({ data, total, loading, error }) => {
                if (loading) { return <Loading />; }
                if (error) { return <p>ERROR</p>; }


                let sum = 0;
                data.forEach(client => {
                    sum = sum + (!isNaN(client.debt) ? client.debt : 0)
                    console.log(client.fname + ' - ' + client.debt)
                });
                console.log(sum);


                return (
                    <div>
                        <p> Πελάτες: {total}, {sum}</p>
                        <ul>
                            {data.map(client => {
                                if ((!isNaN(client.debt) ? client.debt : 0) > 0)
                                    return (
                                        <li key={client.id}>{client.lname} {client.fname} {' - ' + client.debt}€ </li>
                                    )
                                else
                                    return '';

                            })}
                        </ul>
                    </div>
                );
            }}
        </Query>
    );

    return (
        <div className={classes.root}>
            <Grid container spacing={24}>
                <Grid item xs={12}>
                    <Paper className={classes.paper}>
                        Καλωσήρθατε στη διαχείριση του MyGym
                    </Paper>
                </Grid>
                <Grid item xs={6}>
                    <Paper className={classes.paper}>

                        <Typography component="div" style={{ backgroundColor: '#cfe8fc', height: '100vh' }}>

                            {


                                UserList()



                            }



                        </Typography>


                    </Paper>
                </Grid>
                <Grid item xs={6}>
                    <Paper className={classes.paper}>xs=6


                            <Title title="MyGym - Διαχείριση" />
                        <img src="https://newarkironboundfitness.com/wp-content/uploads/2019/02/Depositphotos_82158134_m-2015.jpg" alt="" />
                        <CardContent>Καλωσήρθατε στη διαχείριση του MyGym</CardContent>


                    </Paper>
                </Grid>
                <Grid item xs={3}>
                    <Paper className={classes.paper}>xs=3</Paper>
                </Grid>
                <Grid item xs={3}>
                    <Paper className={classes.paper}>xs=3</Paper>
                </Grid>
                <Grid item xs={3}>
                    <Paper className={classes.paper}>xs=3</Paper>
                </Grid>
                <Grid item xs={3}>
                    <Paper className={classes.paper}>xs=3</Paper>
                </Grid>
            </Grid>
        </div>
    );
}

CenteredGrid.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(CenteredGrid);

