import * as React from "react";

import { PaymentList, PaymentEdit, PaymentCreate, PaymentShow } from './Payments';
import { ClientList, ClientEdit, ClientCreate, ClientShow } from './Clients';
import { UserList, UserEdit, UserCreate, UserShow } from './Users';
import { PlanList, PlanEdit, PlanCreate, PlanShow } from './Plans';
import { SubscriptionList, SubscriptionEdit, SubscriptionCreate, SubscriptionShow } from './Subscriptions';

import Dashboard from './Dashboard';

import UserIcon from '@material-ui/icons/People';
import EuroSymbol from '@material-ui/icons/EuroSymbol';
import AuthSymbol from '@material-ui/icons/Portrait';
import PlanSymbol from '@material-ui/icons/FitnessCenter';

import { Admin, Resource } from "react-admin";
import {
  FirebaseRealTimeSaga,
  FirebaseDataProvider,
  FirebaseAuthProvider
} from "react-admin-firebase";

import CustomLoginPage from './CustomLoginPage';

const firebaseConfig = {
  apiKey: "AIzaSyCICx9NLOuDeFUUZ1aU9Roh1QQ-DkYoVFw",
  authDomain: "gym-manager-app.firebaseapp.com",
  databaseURL: "https://gym-manager-app.firebaseio.com",
  projectId: "gym-manager-app",
  storageBucket: "gym-manager-app.appspot.com",
  messagingSenderId: "442791493546",
  appId: "1:442791493546:web:ffd2d4b1797602ca1d35d3"
};

//test
const options = {
  logging: true,
  rootRef: '',
  watch: ["Clients"]
}
const dataProvider = FirebaseDataProvider(firebaseConfig, options);
const authProvider = FirebaseAuthProvider(firebaseConfig, options);
const firebaseRealtime = FirebaseRealTimeSaga(dataProvider, options);

class App extends React.Component {
  render() {
    return (
      <Admin
        loginPage={CustomLoginPage}
        customSagas={[firebaseRealtime]}
        dataProvider={dataProvider}
        authProvider={authProvider}
        dashboard={Dashboard}
      >

        <Resource name="Clients" list={ClientList} edit={ClientEdit} create={ClientCreate} show={ClientShow} icon={UserIcon} options={{ label: 'Πελάτες' }} />
        <Resource name="Payments" list={PaymentList} edit={PaymentEdit} create={PaymentCreate} show={PaymentShow} icon={EuroSymbol} options={{ label: 'Πληρωμές' }} />
        <Resource name="Subscriptions" list={SubscriptionList} edit={SubscriptionEdit} create={SubscriptionCreate} show={SubscriptionShow} options={{ label: 'Συνδρομές' }} />

        <Resource name="Plans" list={PlanList} edit={PlanEdit} create={PlanCreate} show={PlanShow} icon={PlanSymbol} options={{ label: 'Πακέτα' }} />

        <Resource name="users" list={UserList} edit={UserEdit} create={UserCreate} show={UserShow} icon={AuthSymbol} options={{ label: 'Χρήστες' }} />

      </Admin>
    );
  }
}

export default App;
