import React from 'react';
import {
    List,
    Edit,
    Create,
    Datagrid,
    TextField,
    EditButton,
    SimpleForm,
    FunctionField,
    NumberInput,
    Show,
    SimpleShowLayout,
    ShowButton
} from 'react-admin';

export const PlanList = props => (
    <List title="Πακέτα" {...props}>
        <Datagrid>

            <FunctionField render={plan => `${plan.sessions ? plan.sessions : ''} ${plan.name}`} label="Όνομα" />

            <TextField source="months" label="Μήνες" />
            <FunctionField render={plan => `${plan.price}€`} label="Ποσό" />
            <ShowButton />
            <EditButton />
        </Datagrid>
    </List>
);


export const PlanShow = (props) => (
    <Show {...props}>
        <SimpleShowLayout>
            <TextField source="name" label="Όνομα" />
            <TextField source="months" label="Επισκέψεις" />
            <TextField source="sessions" label="Μήνες" />
            <FunctionField render={plan => `${plan.price}€`} label="Ποσό" />
        </SimpleShowLayout>
    </Show>
);

export const PlanCreate = props => (
    <Create {...props}>
        <SimpleForm>
            <TextField source="name" label="Όνομα" />
            <NumberInput source="sessions" label="Επισκέψεις" />
            <NumberInput source="months" label="Μήνες" />
            <NumberInput source="price" label="Ποσό" />

        </SimpleForm>
    </Create>
);

export const PlanEdit = props => (
    <Edit {...props}>
        <SimpleForm>
            <TextField source="name" label="Όνομα" />
            <NumberInput source="months" label="Μήνες" />
            <NumberInput source="sessions" label="Επισκέψεις" />
            <NumberInput source="price" label="Ποσό" />
        </SimpleForm>
    </Edit>
);