import React from 'react';
import {
    List,
    Edit,
    Show,
    Create,
    Datagrid,
    ReferenceField,
    DateField,
    EditButton,
    ShowButton,
    ReferenceInput,
    SimpleForm,
    TextInput,
    DateTimeInput,
    SimpleShowLayout,
    FunctionField,
    AutocompleteInput,
    NumberInput



} from 'react-admin';


export const PaymentList = props => (
    <List {...props} title="Πληρωμές">
        <Datagrid>
            <ReferenceField source="client" reference="Clients" label="Πελάτης">
                <FunctionField render={client => `${client.lname} ${client.fname}`} />
            </ReferenceField>
            <FunctionField render={payment => `${payment.amount}€`} label="Ποσό" />

            <DateField source="date" label="Ημερομηνία" />
            <EditButton />
            <ShowButton />
        </Datagrid>
    </List >
);

export const PaymentShow = (props) => (
    <Show {...props}>
        <SimpleShowLayout>

            <ReferenceField source="client" reference="Clients" label="Πελάτης">
                <FunctionField render={client => `${client.lname} ${client.fname}`} />
            </ReferenceField>
            <NumberInput source="amount" label="Ποσό" />
            <DateField source="date" label="Ημερομηνία" />
        </SimpleShowLayout>
    </Show>
);


export const PaymentEdit = props => (
    <Edit {...props}>
        <SimpleForm>
            <ReferenceField source="client" reference="Clients" label="Πελάτης">
                <FunctionField render={client => `${client.lname} ${client.fname}`} />
            </ReferenceField>

            <NumberInput source="amount" label="Ποσό" />
            <DateTimeInput source="date" label="Ημερομηνία" />
        </SimpleForm>
    </Edit>
);

export const PaymentCreate = props => (
    <Create {...props}>
        <SimpleForm>
            <ReferenceInput
                label="Πελάτης"
                source="client"
                reference="Clients"

            >
                <AutocompleteInput optionText="fname" />
            </ReferenceInput>

            <TextInput source="amount" label="Ποσό" />
            <DateTimeInput source="date" label="Ημερομηνία" />
        </SimpleForm>
    </Create>
);